/* eslint-disable */
const state = {
  all: {},
  messages: {},
  currentConversation: {},
  unsubscribe: {
    all: {},
    messages: {}
  }
}
const mutations = {
  SET_ALL (state, { conversation }) {
    state.all = {...state.all, [conversation.id]: conversation.data() }
  },
  CLEAR_ALL (state){
    state.all = {}
  },
  UPDATE_CURRENT_CONVERSATION (state, currentConversation) {
    state.currentConversation = currentConversation
  },
  LOAD_MESSAGES (state, { message }) {
    state.messages = {...state.messages, [message.id]: message.data() }
  },
  CLEAR_MESSAGES (state){
    state.messages = {}
  },
  ADD_MESSAGE (state, { conversationId, message }) {
      if (!state.allMsgIds.includes(message.id)) {
          state.all[conversationId].messages.push(message)
          state.allMsgIds.push(message.id)
      }
  }
}
 
const actions = { 
  async loadAll ({ commit, rootState }, option) {
    // console.log(option)
    if (typeof(rootState.conversations.unsubscribe.all) === "function"){
      rootState.conversations.unsubscribe.all()
    }
    let conversationRef = rootState.db.collection('conversations')

    rootState.conversations.unsubscribe.all = await conversationRef.orderBy("updatedAt", "desc").onSnapshot(querySnapshot => {
      commit('CLEAR_ALL')
      if ((typeof(option) != "undefined") && (typeof(option.keyword) != "undefined")){
        //search
        querySnapshot.forEach(conversation => {
          let userId = conversation.data().userId
          let user = rootState.users.all[userId]
          var position = user.displayName.toLowerCase().indexOf(option.keyword.toLowerCase());
          if (position != -1){ commit('SET_ALL', { conversation }) }
        })
      }else if ((typeof(option) != "undefined") && (typeof(option.isRead) != "undefined")){
        //filter
        querySnapshot.forEach(conversation => {
          if (option.isRead){
            if (conversation.data().unreadCount === 0){ commit('SET_ALL', { conversation }) }
          }else{
            if (conversation.data().unreadCount > 0){ commit('SET_ALL', { conversation }) }
          }
        })
      }else{
        //all
        querySnapshot.forEach(conversation => {
          commit('SET_ALL', { conversation })
        })
      }
    })    
    return rootState.conversations.unsubscribe.all;
  },
  async loadMessages ({ commit, rootState }, conversationId) {
    if (typeof(rootState.conversations.unsubscribe.messages) === "function"){
      rootState.conversations.unsubscribe.messages()
    }
    commit('CLEAR_MESSAGES')
    commit('UPDATE_CURRENT_CONVERSATION', {...rootState.conversations.all[conversationId], docId: conversationId})
    let maxMessage = 10000
    let messsageRef = rootState.db.collection('conversations').doc(rootState.conversations.currentConversation.docId).collection('messages').limit(maxMessage).orderBy("updatedAt")
    rootState.conversations.unsubscribe.messages = await messsageRef.onSnapshot(querySnapshot => {
      querySnapshot.forEach(message => commit('LOAD_MESSAGES', { message }))
    })
    return rootState.conversations.unsubscribe.messages;
  },
  async stampTime ({ commit, rootState}, conversationId){
    //change messsage status
    let batch = rootState.db.batch()
    let messsageRef = rootState.db.collection('conversations').doc(conversationId).collection('messages')
    let querySnapshot = await messsageRef.where('isRead', '==', false).get()
    querySnapshot.forEach((doc) => {
      batch.update(doc.ref, {
        isRead: true,
        updatedAt: Date.now()
      })
    });
    batch.commit();
    //update conversation status
    let conversationRef = rootState.db.collection('conversations').doc(conversationId)
    return await conversationRef.update({
      unreadCount: 0
    })
  },
  async sendMessage ({ commit, rootState, dispatch }, text) {
    let conversationId = rootState.conversations.currentConversation.docId
    let adminId = rootState.admins.currentAdmin.docId
    let messageRef = rootState.db.collection('conversations').doc(conversationId).collection('messages')
     //create new message
    let ref = await messageRef.add({
        senderId: adminId,
        senderType: 'admin',
        text: text,
        isRead: false,
        createdAt: Date.now(),
        updatedAt: Date.now()
    });
    //update conversation status
    let conversationRef = rootState.db.collection('conversations').doc(conversationId)
    await conversationRef.update({
      updatedAt: Date.now()
    })
    dispatch('postMessage', text);
  },
  postMessage ({ commit, rootState }, text) {
    let userId = rootState.conversations.currentConversation.userId
    let user = rootState.users.all[userId]
    let channelId = user.channelId;
    let channel = rootState.channels.all[channelId]
    if (channel.provider === "line"){
      let url = '/api/v1/lines/'+channel.id+'/send'
      this.axios.post(url, {
          userId: user.id,
          msg: text,
          type:'text'
      })
      .then(function (response) {
        // handle success
        console.log(response);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
    }
    return
  }
}
export default { namespaced: true, state, mutations, actions }


