/* eslint-disable */
import Vue from 'vue'
import Vuex from 'vuex'
import 'babel-polyfill'
import Firebase from 'Firebase'
import 'Firebase/firestore'
import config from '../config'
import users from './users'
import conversations from './conversations'
import admins from './admins'
import channels from './channels'

const firebase = Firebase.initializeApp(config)
const firestore = firebase.firestore()
firestore.settings({timestampsInSnapshots: true})

Vue.use(Vuex)

const state = {
    db: firestore,
    checkDevie: {
      mfCol3:'',
      mfCol5:'',
    }
}

const mutations = {
  CHECK_DEVIE (state, { isMobile, action }){
    if(isMobile)
      {
        if(action == 'next')
          {
            state.checkDevie.mfCol3 = 'displayNone'
            state.checkDevie.mfCol5 = 'displayBlock'
          }else if(action == 'back')
          {
            state.checkDevie.mfCol3 = 'displayBlock'
            state.checkDevie.mfCol5 = 'displayNone'
          }
      }
  },
}

const actions = {
  async checkDevie ({ commit, rootState }, action) {
    let isMobile = false
    if( navigator.userAgent.match(/Android/i)
      || navigator.userAgent.match(/webOS/i)
      || navigator.userAgent.match(/iPhone/i)
      || navigator.userAgent.match(/iPad/i)
      || navigator.userAgent.match(/iPod/i)
      || navigator.userAgent.match(/BlackBerry/i)
      || navigator.userAgent.match(/Windows Phone/i)
      ){
        isMobile = true
        
      }
      commit("CHECK_DEVIE", { isMobile, action })
  }
}

export default new Vuex.Store({
  state,
  mutations,
  modules: {
    users,
    conversations,
    admins,
    channels
  },
  actions
})



