<template>
  <div>
    <!-- Search-->
    <div id="search">
        <input type="text" placeholder="search" v-model="searchUserText" v-on:keyup.enter="search">
    </div>
    <!-- Status -->
    <div id="chat-status">
        <ul>
            <li :class="isActive('all')" @click="filter('all')"><a>All</a></li>
            <li :class="isActive('read')" @click="filter('read')"><a>Read</a></li>
            <li :class="isActive('unread')" @click="filter('unread')"><a>Unread</a></li>
            <li class="ml-auto"><img src="../assets/images/icon-line.png" id="img-icon"></li>
        </ul>
    </div>
    <!-- List message-->
    <div class="user-container">
      <ul>
        <User  
            v-for="(conversation, index) in conversations" 
            :conversationId="index"
            :userId="conversation.userId"
            :key="conversation.updatedAt"
          />
      </ul>
    </div>

  </div>
</template>
<script>
  
</script>
<style scoped>

</style>

<script>
  /* eslint-disable */
  import User from './User.vue'
  import { mapState, mapGetters, mapActions } from 'vuex'
  export default {
    name: 'UserContainer',
    components: {
      User
    },
    created(){ 
    },
    updated(){
    },
    data () {
        return {
          currentFilter: 'all',
          searchUserText: ''
        }
    },
    mounted: async function(){
      await this.getChannelAll()
      await this.getUserAll()
      await this.getConversationAll()
    },
    computed: {
      ...mapState({
        users: state => state.users.all,
        conversations: state => state.conversations.all,
      }),
      
    },
    methods: {
      ...mapActions({
          getChannelAll: 'channels/loadAll',
          getUserAll: 'users/loadAll',
          getConversationAll: 'conversations/loadAll'
      }),
      filter(tab) {
        this.currentFilter = tab
        this.getConversationAll(this.getOption())
      },
      search(){
        this.getConversationAll(this.getOption())
      },
      getOption() {
        let option = {}
        
        if (this.searchUserText.trim() != ""){
          option.keyword = this.searchUserText
        }
        
        if (this.currentFilter === "all"){
          delete option.isRead
        }else if (this.currentFilter === "read"){
          option.isRead = true
        }else if (this.currentFilter === "unread"){
          option.isRead = false
        }
        return option
      },
      isActive(tab) {
        if (this.currentFilter){
          if (this.currentFilter === tab){
            return 'active'
          }
        }
        return ''
      }
    }
  }
</script>



