<template>
    <span>{{ timeFromNow }}</span>
</template>
<style scoped>

</style>

<script>
  /* eslint-disable */
  export default {
    name: 'Timer',
    mounted: function(){
      
    },
    created(){ 
      this.getTimeFromNow()
      setInterval(this.getTimeFromNow, 60*1000)
    },
    destroyed:function() {
      clearInterval(this.getTimeFromNow)
    },
    watch: {
      startTime: function (newStab, oldStab) {
        clearInterval(this.getTimeFromNow)
        this.getTimeFromNow()
        setInterval(this.getTimeFromNow, 60*1000)
      }
    },
    props: {
      startTime: {
        
      }
    },
    data: function () {
      return {
        timeFromNow: null
      }
    },
    methods: {
      getTimeFromNow () {
        this.timeFromNow = this.$moment(this.startTime).fromNow()
      }
    }
  }
</script>


