<template>

    <li>
      <div id="all-chat" @click="getMessages(conversationId)" v-on:click="checkDevie('next')">
        <a class="link-list" :class="isActive" >
        <!-- <a class="link-list active"> -->
          <div class="detail-message"> 
              <div class="detail-img">
                  <img :src="getAvatar" class="img-profile">
                  <!-- <img src="../assets/images/profile_img.png" class="img-profile"> -->
                  <div class="detail-status active"></div>
              </div>
              <div class="detail-name-time">
                  <div class="detail-name">
                      {{ getDisplayName }}<span> |</span> <span class="text-bold"> {{ getChannelName }}</span>
                  </div>
                  <div class="detail-time">
                      <!--{{ this.conversation.updatedAt | moment("from", "now", true) }}-->
                      <Timer :startTime="conversation.updatedAt"></Timer>
                  </div>
              </div>
              <div class="detail-read active">
                <b-badge pill variant="danger" v-if="conversation.unreadCount > 0">{{ getUnreadCount }}</b-badge>
              </div>
          </div>
        </a>
      </div>
    </li>

</template>
<style scoped>

</style>

<script>
  /* eslint-disable */
  import { mapState, mapGetters, mapActions } from 'vuex'
  import Timer from './Timer.vue'
  export default {
    name: 'User',
    components: {
      Timer
    },
    mounted: function(){
      
    },
    created(){ 
      this.conversation = this.conversations[this.conversationId]
      this.user = this.users[this.userId]
    },
    props: {
      userId: {

      },
      conversationId: {

      }
    },
    data: function () {
      return {
        conversation: {},
        user: {},
        timestamp: new Date
      }
    },
    watch: {
      conversations: function (newStab, oldStab) {
        this.conversation = this.conversations[this.conversationId]
      }
    },
    computed: {
      ...mapState({
        users: state => state.users.all,
        conversations: state => state.conversations.all,
        channels: state => state.channels.all,
        currentConversation: state => state.conversations.currentConversation,
      }),
      isActive () {
        if (this.currentConversation){
          if (this.currentConversation.docId === this.conversationId){
            return 'active'
          }
        }
        return ''
      },
      getDisplayName (){
        if (typeof(this.user.displayName) != "undefined"){
          if (this.user.displayName || this.user.displayName != ""){
            return this.user.displayName
          }
        }
        return 'no name'
      },
      getAvatar (){
        if (typeof(this.user.pictureUrl) != "undefined"){
          if (this.user.pictureUrl || this.user.pictureUrl != ""){
            return this.user.pictureUrl
          }
        }
        return require('../assets/images/profile_img.png')
      },
      getChannelName (){
        if (typeof(this.user.channelId) != undefined){
          if (this.user.channelId || this.user.channelId != ""){
            return this.channels[this.user.channelId].name
          }
        }
        return ''
      },
      getUnreadCount (){
        let max = 99
        if (typeof(this.conversation.unreadCount) != "undefined"){
          if (this.conversation.unreadCount || this.user.unreadCount != ""){
            if (this.conversation.unreadCount > max){
              return '+' + max
            }
            return this.conversation.unreadCount
          }
        }
        return ''
      }
    },
    methods: {
      ...mapActions({
        getMessages: 'conversations/loadMessages',
        checkDevie: "checkDevie"
      })
    }
  }
</script>
<style scoped>
  .badge-danger {
    color: #fff;
    background-color: #F15A5A;
  }
  .badge {
    font-size: 100%;
  }
</style>



